* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
.country-list__flags{
  height: 25px;
  margin-right: 10px;
}
.country-list{
  list-style: none;
}
.country-list__item{
  display: flex;
  margin: 10px;
}
.country-info__flag{
  height: 25px;
}
.country-info__title{
  font-size: 18px;
  font-weight: bold;
}
.country-info__text{
  font-weight: bold;
}
.country-info__container{
  display: flex;
}